import React from 'react';

export default class CertificationSquare extends React.Component {
    render() {

        let styles = {
            paddingBottom : "100px"
        }

        let textStyles = {
            textAlign : "justify"
        }

        return (
            <div style = {textStyles} className="row item">
                    <div className="nine columns">
                      <h2><em>{this.props.certname}</em></h2>
                      <p className="info">{this.props.date}</p>
                    </div>
                    <div style= {styles} className="three columns">
                        <img src={this.props.image} alt="true"/>
                    </div>

                    
            </div> 
        )
        
    }
}