import React from 'react';
import Card from '../skillscard';
import htmlImage from '../../../images/svg/html-5.svg';
import cssImage from '../../../images/svg/css-3.svg';
import pythonImage from '../../../images/svg/python-5.svg';
import phpImage from '../../../images/svg/php-1.svg';
import javaImage from '../../../images/svg/java-4.svg';
import jsImage from '../../../images/svg/javascript.svg';


export default class Languages extends React.Component{
    render(){
        return(
            <div className="cards-container">
                <Card image={javaImage} description="JAVA"/>
                <Card image={pythonImage} description="Python" />
                <Card image={phpImage} description="PHP"/>
                <Card image={htmlImage} description="HTML"/>
                <Card image={cssImage} description="CSS"/>
                <Card image={jsImage} description="Javascript"/>
            </div>

        )
    }
}
