import React from 'react';

export default class EducationSquare extends React.Component {
    render() {
        
        let styles = {
            paddingTop : "50px",
            paddingBottom : "50px"
        }

        let textStyles = {
            textAlign : "justify"
        }

        return (
            <div style = {textStyles} className="row item">
                    <div className="nine columns">
                      <h2><em>{this.props.univname}</em></h2>
                      <p className="info">{this.props.degree} <span>•</span> <em className="date">{this.props.year}</em></p>
                      <p>
                          {this.props.description}
                      </p>
                    </div>
                    <div style= {styles} className="three columns">
                        <img src={this.props.image} alt="true"/>
                    </div>

                    
            </div> 
        )
            
        
    }
}