import React from 'react';
import style from './projectcard.css';

export default class ProjectCard extends React.Component {
render(){
    return (
      <div style={style}>
        <div className="card">
          <img src={this.props.image} style={{width: '100%', height: '100%'}} alt="true"/>
          <h1>{this.props.title}</h1>
          <p className="price">{this.props.category}</p>
          <p className="desc">{this.props.description}</p>
          <p><a href={this.props.link} className="button">Github</a></p>
        </div>
      </div>
    );
  }
}